/* ------------------------------------ *\
    #LIST INLINE
\* ------------------------------------ */

/**
 * List with direct children displaying inline.
 * [1] All items except the first have padding.
 */
.o-list-inline {
    @include list-unstyled();

    & > li {
        display: inline-block;
        vertical-align: middle;
    }
}



