/* ------------------------------------ *\
    #FOOTER
\* ------------------------------------ */

.c-footer {
    overflow: hidden;
    padding: spacer(6) 0 spacer(1);
    color: darken(#fff, 20%);
    position: relative;
    z-index: 0;

    &::after {
        content: '';
        display: block;
        margin-top: spacer(3);
        width: 105%;
        background: color('black');
        clip-path:
            polygon(
                0 0,
                100% 40px,
                100% 100%,
                0 100%
            );
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        z-index: 2;
        transform-origin: left top;
    }
}

.c-footer__canvas {
    display: block;
    height: 150px;
    width: 100%;
    clip-path:
        polygon(
            0 40px,
            100% 0,
            100% 100%,
            0 100%
        );
    top: 0;
    right: 0;
    z-index: 1;
}



