/* ------------------------------------ *\
    #PAGING ARROW
\* ------------------------------------ */

.c-paging-arrow {
    display: block;
    margin: auto;
    width: 50px;
    height: 50px;

    @include media('lg') {
        margin-top: -100px;
        width: 200px;
        height: 200px;
        top: 50vh;
        position: fixed;
        backface-visibility: hidden;
    }
}

.c-paging-arrow--prev {
    @include media('lg') {
        left: -50px;
    }
}

.c-paging-arrow--next {
    @include media('lg') {
        right: -50px;
    }
}

.c-paging-arrow__svg {
    fill: lighten(color('black'), 70%);
    width: 100%;
    height: 100%;
}



