/* ------------------------------------ *\
    #VISIBILITY UTILITIES
\* ------------------------------------ */

/**
 * Visibility classes for all breakpoints
 */
.u-hidden {
    @include loop-breakpoints() {
        display: none;
    }
}


/**
 * Hide the entire tag and it's contents except for screen readers
 */
.u-sr-only {
    @include sr-only();
}



